w td:first-child {\r\n        width: 99%;\r\n    }\r\n}\r\n\r\n/* ---------------------------------------- Card -------------------------------------------- */\r\n\r\n.card-Course-list {\r\n    width: 100%;\r\n    margin-bottom: 15px;\r\n}\r\n\r\n.wrapper {\r\n    width: 100%;\r\n    background: white;\r\n    margin: auto;\r\n    position: relative;\r\n    overflow: hidden;\r\n    border-radius: 4px;\r\n    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);\r\n    transform: scale(0.95);\r\n    transition: box-shadow 0.5s, transform 0.5s;\r\n}\r\n\r\n.wrapper:hover {\r\n    transform: scale(1);\r\n    box-shadow: 5px 20px 30px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.wrapper .card-container-course-list {\r\n    width: 100%;\r\n    height: 100%;\r\n}\r\n\r\n.wrapper .card-container-course-list .bottom {\r\n    width: 200%;\r\n    height: 20%;\r\n    transition: transform 0.5s;\r\n}\r\n\r\n.wrapper .card-container-course-list .bottom.clicked {\r\n    transform: translateX(-50%);\r\n}\r\n\r\n.wrapper .card-container-course-list .bottom h1 {\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\n.wrapper .card-container-course-list .bottom p {\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\n.wrapper .card-container-course-list .bottom .left {\r\n    height: 100%;\r\n    width: 50%;\r\n    background: #fff;\r\n    position: relative;\r\n    float: left;\r\n}\r\n\r\n.wrapper .card-container-course-list .bottom .left .details {\r\n    padding: 20px 0 10px 15px;\r\n    float: left;\r\n}\r\n\r\n.wrapper .card-container-course-list .bottom .left .details h6 {\r\n    font-size: 12px;\r\n}\r\n\r\n.wrapper .inside {\r\n    z-index: 9;\r\n    background: #268b45;\r\n    width: 140px;\r\n    height: 140px;\r\n    position: absolute;\r\n    top: -70px;\r\n    right: -70px;\r\n    border-radius: 0px 0px 200px 200px;\r\n    transition: all 0.5s, border-radius 2s, top 1s;\r\n    overflow: hidden;\r\n}\r\n\r\n.wrapper .inside .icon {\r\n    position: absolute;\r\n    right: 8